.our-projects {
	padding-top: 60px;
	padding-bottom: 30px;

	&__inner {
		overflow-x: hidden;
		@media screen and (max-width: $phone-size-max) {
			padding-right: 15px;
			padding-left: 15px;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
		margin-bottom: 16px;
		@media screen and (max-width: $phone-size-max) {
			margin: 0;
			padding-top: 30px;
		}
	}

	&__item {
		flex-basis: 50%;
		max-width: 50%;
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 14px;

		@media screen and (max-width: $phone-size-max) {
			padding: 0;
			margin-bottom: 30px;
			max-width: 100%;
			flex-basis: 100%;

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}

.our-project {
	display: flex;

	@media screen and (max-width: $phone-size-max) {
		flex-wrap: wrap;
	}

	&__img {
		flex-basis: 47.35%;
		max-width: 47.35%;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;

		@media screen and (max-width: $phone-size-max) {
			height: 200px;
			min-height: 200px;
			flex-basis: 100%;
			max-width: 100%;

		}
	}

	&__main {
		flex-basis: 52.65%;
		max-width: 52.65%;
		background: #F9F9F9;
		padding: 40px;

		@media screen and (max-width: $phone-size-max) {
			padding: 30px 25px;
			flex-basis: 100%;
			max-width: 100%;
		}
	}

	&__title {
		font-weight: 300;
		font-size: 18px;
		line-height: 120%;
		color: #4B4B4B;
		margin-bottom: 20px;
	}

	&__infos {
		font-size: 13px;
		line-height: 16px;
		color: #968F8F;
		margin-bottom: 20px;

		span {
			text-decoration: underline;
		}
	}

	&__info {
		margin-bottom: 3px;
	}

	&__descr {
		font-weight: 300;
		font-size: 14px;
		line-height: 21px;
		color: #4B4B4B;
		margin-bottom: 30px;
	}
}