.calculation-block {

	padding-top: 30px;
	padding-bottom: 30px;

	&__inner {
		background: #F9F9F9;
		display: flex;
		@media screen and (max-width: $phone-size-max) {
			flex-wrap: wrap;
		}
	}

	&__form {
		flex-basis: 67%;
		max-width: 67%;
		padding-left: 50px;
		padding-top: 50px;
		padding-bottom: 50px;

		@media screen and (max-width: $phone-size-max) {
			order: 1;
			flex-basis: 100%;
			max-width: 100%;
			margin-top: -138px;
			padding-top: 30px;
			padding-left: 15px;
			padding-right: 15px;
			padding-bottom: 30px;
		}
	}

	&__img {
		flex-basis: 33%;
		max-width: 33%;
		background-image: url("../img/calculation-form-bg-1.jpg");
		background-size: cover;
		clip-path: polygon(0 0, 100% 0, 100% 100%, 17% 100%);
		background-position: center center;
		background-repeat: no-repeat;
		min-height: 100%;
		position: relative;
		background-color: #F9F9F9;
		&::after {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(80.72deg, #F9F9F9 4.57%, rgba(249, 249, 249, 0) 100%);
		}

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			max-width: 100%;
			min-height: 138px;
			height: 138px;
			order: 0;
			clip-path: none;
			opacity: 0.1;

			&::after {
				background: none;
			}
		}
	}
}

.calculation-form {
	&__title {
		margin-bottom: 20px;
		@media screen and (max-width: $phone-size-max) {
			font-weight: 500;
			font-size: 22px;
			line-height: 26px;
			margin-bottom: 58px;
		}
	}
	&__subtitle {
		margin-bottom: 30px;
		@media screen and (max-width: $phone-size-max) {
			margin-bottom: 20px;
		}
	}
	&__input {
		display: block;
		flex-basis: 32.5%;
		max-width: 32.5%;
		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			max-width: 100%;
			margin-bottom: 20px;
			&:last-of-type {
				margin-bottom: 30px;
			}
		}
	}
	&__line {
		display: flex;
		justify-content: space-between;
		@media screen and (max-width: $phone-size-max) {
			flex-direction: column;
		}
	}
	&__button {
		padding-right: 65px;
		padding-left: 65px;
	}
}