.services {

	&__inner {
		padding-top: 30px;
		padding-bottom: 30px;
		overflow-x: hidden;

		@media screen and (max-width: $phone-size-max) {
			padding-right: 15px;
			padding-left: 15px;
		}
	}

	&__title {
		font-size: 20px;
		line-height: 160%;
		letter-spacing: 0.03em;
		@media screen and (max-width: $phone-size-max) {
			line-height: 24px;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;

		@media screen and (max-width: $phone-size-max) {
			margin: 0;
		}
	}

	&__item {
		flex-basis: 25%;
		max-width: 25%;
		padding-left: 15px;
		padding-right: 15px;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			max-width: 100%;
			padding-left: 0;
			padding-right: 0;
			margin-bottom: 30px;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&-block {
		width: 100%;
		&__img {
			margin-bottom: 20px;
			height: 220px;
			width: 100%;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;
		}

		&__title {
			font-size: 16px;
			line-height: 19px;
			color: #4B4B4B;
			margin-bottom: 10px;

			@media screen and (max-width: $phone-size-max) {
				padding-right: 10px;
			}
		}

		&__link {
			font-weight: 300;
			font-size: 13px;
			line-height: 18px;
			color: #968F8F;
			transition: .3s ease-out;

			&:hover {
				text-decoration: underline;
				color: #EC1227;
			}
		}
	}
}