.banner {

	&__inner {
		background-image: url("../img/banner-bg.jpg");
		background-position: center center;
		background-size: cover;
		display: flex;
		justify-content: space-between;
		align-items: center;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		@media screen and (max-width: $phone-size-max) {
			box-shadow: none;
		}
	}

	&__main {
		background: linear-gradient(180deg, #EC1227 0%, rgba(242, 107, 141, 0.8) 100%);
		flex-basis: 40.2%;
		padding-top: 60px;
		padding-right: 50px;
		padding-left: 50px;
		min-height: 430px;
		color: #fff;
		font-family: $ff-main;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			padding-top: 37px;
			padding-left: 15px;
			padding-right: 15px;
		}

		&-btn {
			display: none;
			@media screen and (max-width: $phone-size-max) {
				display: inline-block;
				margin-top: 15px;
			}
		}
	}


	&__title {
		font-weight: bold;
		font-size: 30px;
		line-height: 140%;
		margin-bottom: 40px;

		@media screen and (max-width: $phone-size-max) {
			line-height: 36px;
			margin-bottom: 30px;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 24px;
		margin-bottom: 15px;

		:last-child {
			margin-bottom: 0;
		}
	}
}

.banner__form {
	flex-basis: 333px;
	margin-right: 45px;

	@media screen and (max-width: $phone-size-max) {
		display: none;
	}
}
.form-consult {
	background-color: #fff;
	padding: 45px;
	border: 2px solid #EC1227;
	width: 100%;

	&__title {
		margin-bottom: 24px;
		letter-spacing: -0.008em;
	}

	&__input {
		display: block;

		&:first-of-type {
			margin-top: 30px;
		}	
		margin-bottom: 18px;

		&:last-of-type {
			margin-top: 0;
			margin-bottom: 30px;
		}
	}
}