.our-project-photo {
	padding-top: 30px;
	padding-bottom: 25px;

	&__inner {

		@media screen and (max-width: $phone-size-max) {
			padding-right: 15px;
			padding-left: 15px;
		}

	}


	&__nav {
		display: flex;
		align-items: center;
	}

	&__dots {
		transform: translateY(-2px);
		.slick-dots {
			display: flex;

			& > * {
				margin-right: 10px;
				&:last-of-type {
					margin-right: 0;
				}
			}

			& > *.slick-active {
				.our-project-photo__dot {
					background: #EC1227;
				}
			}
		}
	}
	&__next, &__prev {
		background-color: transparent;
		border: none;
		cursor: pointer;
		display: block;
		fill: #D9DDE2;
		svg {
			max-width: 12px;
			max-height: 20px;
			fill: #C0C6CE;
			transition: .3s ease-out;
			&:hover {
				fill: #939393;
			}
		}

		&:hover {
			svg {
				color: #939393;
			}
		}
	}

	&__next{
		margin-left: 25px;
	}

	&__prev {
		margin-right: 25px;
	}

	&__dot {
		display: block;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		border: 0px;
		background: #C4C4C4;
		transition: .3s ease-out;
		cursor: pointer;

		& + button {
			display: none;
		}

		&:hover {
			background-color: #939393;
		}
	}
}

.our-project-photo-slick {
	overflow-x: hidden;
	margin-bottom: 22px;

	.slick-list {
		margin: 0 -15px;
	}


	&__img {
		height: 400px;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		margin-right: 15px;
		margin-left: 15px;

		@media screen and (max-width: $phone-size-max) {
			height: 220px;
		}
	}
}