.footer{
	padding-top: 30px;

	&__inner {
		border-top: 1px solid #4B4B4B;
		padding-top: 30px;
		padding-bottom: 30px;

		@media screen and (max-width: $phone-size-max) {
			padding-right: 15px;
			padding-left: 15px;
		}
	}

	&__top-line {
		display: flex;

		@media screen and (max-width: $phone-size-max) {
			flex-wrap: wrap;
		}
	}

	&__col-1 {
		flex-basis: 23.3%;
		// max-width: 23.3%;
		padding-bottom: 15px;
		@media screen and (max-width: $phone-size-max) {
			padding-bottom: 0;
			flex-basis: 100%;
			margin-bottom: 28px;
		}
	}

	&__col-2 {
		flex-basis: 20%;
		// max-width: 20%;
		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
		}
	}

	&__col-3 {
		flex-basis: 30%;
		// max-width: 30%;
		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			margin-bottom: 30px;
		}
	}

	&__col-4 {
		flex-basis: 26.7%;
		// max-width: 26.7%;
		text-align: right;
		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			margin-bottom: 30px;
		}
	}

	&-logo {
		&__img {
			margin-bottom: 15px;
			max-width: 155px;
			display: block;

			@media screen and (max-width: $phone-size-max) {
				margin-bottom: 0;
				max-width: 105px;
			}
		}

		&__subtitle {
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			color: #968F8F;
		}
	}

	&__copy {
		font-size: 12px;
		line-height: 14px;
		float:left;
		margin-top: -14px;
		color: #968F8F;
		font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
		@media screen and (max-width: $phone-size-max) {
			float: none;
			margin-top: 0;
		}
	}

	// nav

	&-nav {

		@media screen and (max-width: $phone-size-max) {
			display: flex;
			justify-content: space-between;
		}

		&__link {
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			margin-bottom: 15px;
			display: block;
			color: #968F8F;
			&:last-of-type {
				margin-bottom: 0;
			}
			transition: .3s ease-out;

			&:hover {
				color: #4B4B4B;
			}

			@media screen and (max-width: $phone-size-max) {
				font-size: 12px;
				line-height: 14px;
				margin-bottom: 0;
				text-transform: uppercase;
			}
		}
	}

	&-menu {

		&__link {
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			display: block;
			letter-spacing: -0.015em;
			color: #4F4F4F;
			margin-bottom: 14px;
			&:last-of-type {
				margin-bottom: 0;
			}

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&-info {
		font-size: 18px;
		line-height: 30px;
		text-align: right;
		color: #968F8F;
		text-align: right;
		margin-bottom: 20px;

		a {
			color: #968F8F;
			transition: .3s ease-out;

			&:hover {
				color: #4B4B4B;
			}
		}

		@media screen and (max-width: $phone-size-max) {
			font-size: 14px;
			line-height: 24px;
			text-align: left;
			margin-bottom: 0;
		}
	}
}