.stone-cottage-info {

	padding-top: 30px;
	padding-bottom: 30px;

	&__inner {
		background: linear-gradient(180deg, #F9F9F9 0%, rgba(249, 249, 249, 0) 100%);
		display: flex;

		@media screen and (max-width: $phone-size-max) {
			flex-wrap: wrap;
		}
	}

	&__img {
		flex-basis: 31.5%;
		max-width: 31.5%;
		min-height: 100%;
		background-repeat: no-repeat;
		background-position: -75px center;
		background-size: cover;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			max-width: 100%;
			min-height: 220px;
			background-position: center center;
		}
	}

	&__body {
		padding-top: 73px;
		padding-bottom: 73px;
		padding-left: 60px;
		padding-right: 60px;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			max-width: 100%;

			padding: 30px 15px;
		}
	}

	&__title {
		margin-bottom: 29px;
	}

	&__subtitle {
		font-size: 16px;
		line-height: 26px;
		padding-right: 0;
		margin-bottom: 16px;
		color: #968F8F;
	}
}

// material part

.stone-cottage-material {
	padding-top: 30px;
	padding-bottom: 30px;

	&__inner {
		height: auto;
		overflow: hidden;

		@media screen and (max-width: $phone-size-max) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__list {
		display: flex;
		margin: 0 -15px;
		flex-wrap: wrap;

		@media screen and (max-width: $phone-size-max) {
			flex-wrap: wrap;
		}
	}

	&__item {
		flex-basis: 25%;
		max-width: 25%;
		padding-left: 15px;
		padding-right: 15px;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 100%;
			margin-bottom: 40px;
			max-width: 100%;

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__img {
		height: 220px;
		width: 100%;
		margin-bottom: 25px;

		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
	}

	&__title {
		font-size: 18px;
		line-height: 22px;
		color: #4B4B4B;
		margin-bottom: 15px;
	}

	&__text {
		font-weight: 300;
		font-size: 13px;
		line-height: 18px;
		color: #968F8F;
	}
}