.our-features {
	padding-top: 30px;
	padding-bottom: 30px;

	&__inner {
		@media screen and (max-width: $phone-size-max) {
			padding-right: 15px;
			padding-left: 15px;
		}
	}

	&__list {
		margin-top: 60px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;

		@media screen and (max-width: $phone-size-max) {
			justify-content: space-between;
			margin-bottom: -60px;
		}
	}

	&__item {
		flex-basis: 20%;
		max-width: 20%;
		display: flex;
		flex-direction: column;
		align-items: center;

		@media screen and (max-width: $phone-size-max) {
			flex-basis: 44%;
			max-width: 44%;
			margin-bottom: 60px;
		}
	}

	&__img {
		margin-bottom: 35px;
		max-width: 90px;
		svg {
			max-width: 100%;
			height: auto;
		}

		@media screen and (max-width: $phone-size-max) {
			max-width: 58px;
			margin-bottom: 30px;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 22px;
		color: #4B4B4B;
		text-align: center;
	}
}