.breadcrumb-block {
	padding-top: 30px;
	padding-bottom: 30px;

	@media screen and (max-width: $phone-size-max) {
		padding-top: 20px;
		padding-bottom: 20px;
	}

	&__inner {
		@media screen and (max-width: $phone-size-max) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}
}

.breadcrumb {
	display: flex;

	font-weight: 300;
	font-size: 12px;
	line-height: 14px;
	letter-spacing: -0.045em;
	color: #968F8F;

	a {
		text-decoration: underline;
		color: #968F8F;
	}

	&__item {
		margin-right: 17px;
		position: relative;

		&::after {
			display: block;
			content: "|";
			position: absolute;
			@include centerY;
			right: -11px;
		}

		&:last-child::after {
			display: none;
		}
		
	}
}