.container {
	width: 1200px;
	margin-left: auto;
	margin-right: auto;

	@media screen and (max-width: $phone-size-max) {
		width: 320px;
	}
}

.dec-line-light {
	background-color: #D4D4D4;
	height: 1px;
	width: 100%;
}

.lock {
	overflow-y: hidden;
}

.center {
	text-align: center;
}

.btn {
	padding: 12px 38px 11px 38px;
	background-color: transparent;
	border-radius: 0;
	border-width: 1px;
	border-style: solid;
	display: inline-block;
	cursor: pointer;
	font-size: 14px;
	line-height: 17px;
	text-transform: uppercase;
	font-family: $ff-main;
	letter-spacing: -0.015em;
	transition: .3s ease-out;
}

.btn.btn-fluid {
	width: 100%;
	padding: 12px 0px 11px 0px;
}

.btn.btn-primary {
	border-color: #ED003A;
	color: #ED003A;

	&:hover {
		color: #FFFFFF;
		background-color: #ED003A;
	}
}

.btn.btn-light {
	color: #FFFFFF;
	border-color: #FFFFFF;
}

.btn-sm {
	padding-left: 25px;
	padding-right: 25px;
	padding-top: 12px;
	padding-bottom: 11px;
}

.btn.btn-secondary {
	color: #968F8F;
	border-color: #968F8F;

	&:hover {
		color: #FFFFFF;
		border-color: #ED003A;
		background-color: #ED003A;
	}
}

// form

.form {
	&__title {
		font-weight: 500;
		font-size: 20px;
		line-height: 24px;
		color: #4B4B4B;
	}
	&__subtitle {
		font-size: 16px;
		line-height: 160%;
		letter-spacing: 0.01em;
		color: #968F8F;
	}
	&__input {
		cursor: pointer;
		border: 1px solid #D7D7D7;
		width: 100%;
		padding: 11px 22px 10px 22px;
		background-color: #fff;
		color: #4B4B4B;
		transition: .3s ease-out;

		@include placeholder{
			font-weight: 300;
			font-size: 14px;
			line-height: 17px;
			text-transform: uppercase;
			color: #968F8F;
		}

		&:hover {
			border: 1px solid #4B4B4B;
			
			@include placeholder{
				color: #4B4B4B;
			}
		}
	}
}

// titles

.section__title {
	font-weight: 500;
	font-size: 22px;
	line-height: 26px;
	color: #4B4B4B;
	margin-bottom: 30px;
}

.section__title-sm {
	font-size: 20px;
}

.section__subtitle-thin {
	font-weight: 300;
}

.section__subtitle {
	font-size: 15px;
	line-height: 26px;
	color: #968F8F;
	margin-bottom: 30px;
	padding-right: 17%;

	@media screen and (max-width: $phone-size-max) {
		padding-right: 0;
	}
}


// list

.list {
	padding-left: 20px;
	&_left-25 {
		margin-left: 25px;
		@media screen and (max-width: $phone-size-max) {
			margin-left: 0;
		}
	}
	
	@media screen and (max-width: $phone-size-max) {
		padding-left: 28px;
	}
	&__item {
		list-style-type: disc;
		font-size: 15px;
		line-height: 26px;
		color: #968F8F;
		padding-left: 5px;
	}

	& > * {
		margin-bottom: 13px;
		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&__header {
		font-size: 16px;
		line-height: 26px;
		color: #968F8F;
		list-style-type: none;
		margin-left: -20px;
		margin-bottom: 30px;

		@media screen and (max-width: $phone-size-max) {
			margin-bottom: 18px;
			margin-left: -29px;
		}
	}
}

// text

.title {
	font-size: 22px;
	line-height: 26px;
	letter-spacing: 0.03em;
	color: #4B4B4B;
}

.subtitle {
	font-size: 20px;
	line-height: 24px;
	letter-spacing: 0.03em;
	color: #4B4B4B;
}

.paragraph {
	font-weight: 300;
	font-size: 14px;
	line-height: 160%;
	letter-spacing: 0.03em;
	color: #4F4F4F;
}

.mb-60 {
	margin-bottom: 60px;
}
.mb-30 {
	margin-bottom: 30px;
}
.mb-20 {
	margin-bottom: 20px;
}

// other
.hide-phone {
	@media screen and (max-width: $phone-size-max) {
		display: none;
	}
}