body{
	font-family: $ff-main;
	line-height: 1;
	font-size: 16px;
	font-style: normal;
	font-weight: normal;
}
*, *:focus{
	outline: 0;
}
html{
	box-sizing: border-box;
}
*, *:after, *:before{
	box-sizing: inherit;
}
ul, ol{
	list-style-type: none;
}
.clearfix{
	&:after{
		content: '';
		display: table;
		clear: both;
	}
}
a{
	text-decoration: none;
}
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}