.mfp-bg {
	opacity: 0.2;
}

.mfp-inline-holder .mfp-content {
	width: auto;
}

.popup-form-consult {
	width: 290px;
}

.popup {
	position: relative;

	&__close {
		position: absolute;
		top: 15px;
		right: 15px;
		font-size: 10px;
		width: 10px;
		height: 10px;
		cursor: pointer;

		svg {
			width: 10px;
			height: 10px;
			fill: #968F8F;
		}

		&:hover {
			svg {
				fill: #4B4B4B;
			}
		}
	}
}

.popup-form-consult .form-consult {
	padding-left: 35px;
	padding-right: 35px;

	&__title {
		font-size: 18px;
		line-height: 22px;
	}
}