.our-facts {

	&__inner {
		background: #F9F9F9;
		padding: 50px;

		@media screen and (max-width: $phone-size-max) {
			padding: 60px 15px;
		}
	}

	&__list {
		display: flex;
		justify-content: space-between;

		@media screen and (max-width: $phone-size-max) {
			flex-direction: column;
		}
	}

	&__item {
		display: flex;
		flex-basis: 28.5%;
		align-items: center;

		@media screen and (max-width: $phone-size-max) {
			margin-bottom: 28px;
			min-height: 52px;

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__img {
		margin-right: 35px;

		@media screen and (max-width: $phone-size-max) {
			margin-right: 25px;
		}

		svg {
			height: auto;
			max-width: 64px;

			@media screen and (max-width: $phone-size-max) {
				max-width: 52px;
			}
		}
	}

	&__text {
		font-size: 18px;
		line-height: 22px;
		letter-spacing: -0.015em;

		flex-basis: auto;

		color: #4B4B4B;

		@media screen and (max-width: $phone-size-max) {
			font-size: 16px;
			line-height: 19px;
		}
	}
}