// HEADER

.header {

	&__inner {
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid #D3D3D3;
	}

	// top

	&__top {
		background-color: #F9F9F9;
		display: flex;
		justify-content: flex-end;
		padding-top: 20px;
		padding-bottom: 20px;
		padding-right: 20px;

		@media screen and (max-width: $phone-size-max) {
			order: 1;
			padding-right: 0px;
			background-color: transparent;
			padding-top: 15px;
			padding-bottom: 15px;
		}
	}

	&-nav {
		margin-right: 80px;
		@media screen and (max-width: $phone-size-max) {
			margin-right: 0;
			width: 100%;
		}
		&__list {
			display: flex;
			@media screen and (max-width: $phone-size-max) {
				justify-content: space-around;
			}
		}
		&__item	{
			margin-right: 50px;
			@media screen and (max-width: $phone-size-max) {
				margin-right: 0;
			}
			a {
				display: block;
				font-family: $ff-main;
				font-weight: 300;
				font-size: 14px;
				line-height: 17px;
				text-transform: uppercase;
				color: #968F8F;
				transition: .3s ease-out;
				@media screen and (max-width: $phone-size-max) {
					font-size: 12px;
					line-height: 14px;
				}
				&:hover {
					color: #4B4B4B;
				}
			}
			&:last-child {
				margin-right: 0;
			}
		}
	}

	&-auth {
		color: #4B4B4B;
		font-size: 14px;
		line-height: 17px;
		cursor: pointer;
		display: flex;
		letter-spacing: -0.015em;
		text-transform: uppercase;
		transition: .3s ease-out;
		font-family: $ff-main;
		@media screen and (max-width: $phone-size-max) {
			display: none;
		}
		// transition: .3s;
		&__img {
			width: 12px;
			height: 10px;
			margin-right: 8px;
			margin-top: 2px;
		}

		&:hover {
			color: #EC1227;
		}
	}

	&__main-auth {
		display: none;
		margin-right: auto;

		@media screen and (max-width: $phone-size-max) {

			&.active {
				display: flex;
				position: relative;
				z-index: 5;
			}
		}
	}

	// main

	&__main {
		display: flex;
		align-items: center;
		padding-top: 20px;
		padding-bottom: 20px;
		border-bottom: 1px solid #4B4B4B;

		@media screen and (max-width: $phone-size-max) {
			order: 0;
			padding-top: 6px;
			padding-bottom: 6px;
			padding-left: 15px;
			padding-right: 15px;
			position: relative;

			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				width: 100%;
				height: calc(100% + 1px);
				background-color: #fff;
				z-index: 3;
				border-bottom: 1px solid #4B4B4B;
			}
		}
	}

	&__logo {
		margin-right: auto;
		display: flex;
		align-items: center;
		display: none;

		@media screen and (max-width: $phone-size-max) {
			position: relative;
			z-index: 5;
		}

		&.active {
			display: flex;
		}

		img {
			width: 154px;
			height: 30px;

			@media screen and (max-width: $phone-size-max) {
				width: 105px;
				height: 20px;
			}
		}
	}

	&__cons {
		margin-right: 52px;
		display: flex;
		align-items: center;

		@media screen and (max-width: $phone-size-max) {
			display: none;
		}
	}

	&__info {
		margin: 0;

		@media screen and (max-width: $phone-size-max) {
			margin-right: 15px;
			position: relative;
			z-index: 5;
		}
	}

	&__tel {
		font-weight: 500;
		font-size: 20px;
		line-height: 24px;
		font-family: $ff-main;
		letter-spacing: -0.015em;
		color: #4B4B4B;
		display: block;
		margin-bottom: 6px;

		@media screen and (max-width: $phone-size-max) {
			font-size: 14px;
			line-height: 17px;
		}
	}

	&__days {
		font-size: 13px;
		line-height: 16px;
		letter-spacing: -0.015em;
		color: #968F8F;
		text-align: right;

		@media screen and (max-width: $phone-size-max) {
			font-size: 12px;
			line-height: 14px;
		}
	}

	// menu

	&-menu {
		padding-top: 14px;
		padding-bottom: 14px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-right: 2px;
		padding-left: 2px;
		transition: .3s ease-out;

		@media screen and (max-width: $phone-size-max) {
			order: 2;
			width: 320px;
			height: 100%;
			justify-content: flex-start;
			& > * {
				margin-bottom: 50px;
			}
			z-index: 2;
			@include centerX();
			position: fixed;
			background-color: #fff;
			flex-direction: column;
			padding: 0;
			top: -100%;
			padding-top: 100px;

			&.active {
				// padding-top: 20%;
				top: 0;
			}
		}

		&__item {
			color: #4F4F4F;
			font-weight: 300;
			font-size: 13px;
			line-height: 16px;
			letter-spacing: -0.015em;
			// padding-right: 50px;
			transition: .3s ease-out;

			&:last-child {
				padding-right: 0;
			}

			&.active{
				font-weight: normal;
			}

			&:hover {
				text-decoration: underline;
			}

			@media screen and (max-width: $phone-size-max) {
				padding: 0px 50px;
				width: 100%;
				text-align: center;
				&:last-child {
					padding-right: 50px;
				}
			}
		}

	}

	// burger

	.header-burger {
		display: none;
		@media screen and (max-width: $phone-size-max) {
			display: block;
		}
		position: relative;
		width: 20px;
		height: 15px;
		z-index: 5;
		cursor: pointer;

		span {
			display: block;
			top: 6px;
			position: absolute;
			background-color: #4B4B4B;
			width: 100%;
			height: 3px;
			left: 0;
			transition: 0.3s;
		}

		&:before,&:after {
			display: block;
			content: "";
			position: absolute;
			background-color: #4B4B4B;
			width: 100%;
			height: 3px;
			left: 0;
			transition: 0.3s;
		}

		&.active span {
			transform: scale(0);
		}

		&.active:before {
			transform: rotate(45deg);
			top: 6px;
		}

		&.active:after {
			transform: rotate(-45deg);
			bottom: 6px
		}

		&:before{
			top: 0;
		}
		&:after{
			bottom: 0;
		}
	}
}